.PageHeader-container {
    background-color: rgba(90, 88, 101, 0.17);
    padding: 30px;
    border-radius: 20px;
    text-align: center;
    margin-bottom: 100px;
}

.PageHeader-title {
    font-family: var(--font-rubik);
    position: relative;
    padding-bottom: 10px;
    font-weight: 600;
    color: var(--color-white);
}

.PageHeader-title::after {
    position: absolute;
    content: "";
    height: 2px;
    bottom: 0;
    margin: 0 auto;
    left: 0;
    right: 0;
    width: 10%;
    background: var(--color-dark-green);
}

.PageHeader-content {
    padding: 0 100px;
}
