.navbar {
    background-color: rgba(90, 88, 101, 0.17);
}

.navbar-brand img {
    width: 50px;
}

.nav-item {
    font-family: var(--font-rubik);
    font-weight: 600;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 1.5px;
}

.nav-link {
    color: var(--color-white);
}

.nav-link:hover {
    color: var(--color-light-green) !important;
}