.btn {
    margin-top: 15px;
    transition: 0.3s;
}

.btn:hover {
    transform: translate(0, -5px);
}

.btn-primary {
    background-color: var(--color-dark-green);
    border-color: var(--color-dark-green);
    border-radius: 1px;
    padding: 10px 20px;
}

.btn-primary:hover {
    background-color: var(--color-light-green);
    border-color: var(--color-light-green);
}