@import url('https://fonts.googleapis.com/css2?family=Karla:wght@300;400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@600;700&display=swap');

:root {
    --color-light-green: #009d66;
    --color-dark-green: #00844d;
    --color-white: #ececec;
    --color-light-gray: #999999;
    --color-dark-gray: #111010;
    --font-karla: 'Karla', sans-serif;
    --font-rubik: 'Rubik', sans-serif;
}

body {
    font-family: var(--font-karla);
    color: var(--color-light-gray);
    background-color: var(--color-dark-gray);
    /* background-image: url(../src/assets/quadromous-bg-circle.png); */
}